import React, { useEffect, useState } from 'react';  
import { DownloadOutlined } from '@ant-design/icons';
import { Button, Tabs } from 'antd';
import { Card } from 'antd';

const { TabPane } = Tabs;

//  ant-design Tabs
//  markdown 

const RightTab = ({ inputValue }) => {  

  // useState  preOutput 
  const [outputString, preOutput] = useState('');  
  
  //  useEffect inputValue 
  useEffect(() => {  
    const htmlString = changeHtml(inputValue);  
    preOutput(htmlString);  
  }, [inputValue]);  
  
  //  inputValue  html 
  //  runoob 
  function changeHtml(value){  
    let html = value;
    //  
    html = html.replace(/######\s+(.*)/g, '<h6>$1</h6>');
    html = html.replace(/#####\s+(.*)/g, '<h5>$1</h5>'); 
    html = html.replace(/####\s+(.*)/g, '<h4>$1</h4>');
    html = html.replace(/###\s+(.*)/g, '<h3>$1</h3>');  
    html = html.replace(/##\s+(.*)/g, '<h2>$1</h2>');  
    html = html.replace(/#\s+(.*)/g, '<h1>$1</h1>');  
    // 
    html = html.replace(/\*\*\*(.*)\*\*\*/g, '<b><i>$1</i></b>');
    // 
    html = html.replace(/\*\*(.*)\*\*/g, '<b>$1</b>');
    // 
    html = html.replace(/\*(.*)\*/g, '<i>$1</i>');
    //   
    html = html.replace(/\*\s+(.*)/g, '<ul><li>$1</li></ul>');  
    // 
    html = html.replace(/`(.*)`/g, '<code>$1</code>');
    // 
    html = html.replace(/\[(.*?)\]\((.*?)\)/g, '<a href="$2">$1</a>');  
    //   
    html = html.replace(/(.*)(?:\r\n|\r|\n)/g, '<p>$1</p>');

    return html;  
  };  
  
  // 
  const operations = (  
    <Button type="primary" icon={<DownloadOutlined />} size="middle"
     onClick={downloadString}>  
      HTML  
    </Button>  
  );  

  function downloadString(){
    const loadString = `
    <!DOCTYPE html>
    <html>
    <head>
    <meta charset="utf-8">
    <title>markdown -EDTR</title>
    </head>
    <body>
        ${outputString}
    </body>
    </html>
    `;
    
    //  chatGPT
    const element = document.createElement('a');  //  `<a>` 
    const file = new Blob([loadString], { type: 'text/html' });  //  `Blob`  `loadString` 
    element.href = URL.createObjectURL(file);  //  `<a>`  `href`  URL
    let downTime = Date();
    downTime = downTime.replace(/\s/g, '-');
    downTime = downTime.slice(4,23);
    element.download = `markdown_${downTime}.html`;  //
    element.style.display = 'none'; // 
    document.body.appendChild(element);  //  `<a>`  `<body>` 
    element.click();  //
    document.body.removeChild(element);  
  }
  
  return (  
    <Tabs  
      defaultActiveKey="1"  
      size="middle"  
      tabBarExtraContent={operations}  
      style={{ width: '65vh' }}  
    >  
      <TabPane tab="markdown" key="1">  
        <Card  
          style={{  
            height: '70vh',  
            width: '70vh',
            textAlign: 'left',  
          }}  
        >  

          <div dangerouslySetInnerHTML={{ __html: outputString }}></div> 
          {/* chatGPT: ReactXSS
          HTMLHTML
          HTML`dangerouslySetInnerHTML`React
          ReactHTML */}
        </Card>  
      </TabPane>  
    </Tabs>  
  );  
};  

export default RightTab;