import React from 'react';
import { Layout } from 'antd';
import Contenter from './Contenter';
const { Content } = Layout;

//  ant-design 
// 

const MainContent = () => {

  return (

      <Content
        style={{
          height: '84vh',
          padding: '0',
          background: '#d6e4ff' // ant-design-geekblue-2
        }}
      >
        <Contenter/>

      </Content>

  );
};

export default MainContent;