import React, { useState } from 'react';
import { Button, Drawer } from 'antd';
import { Divider, Typography } from 'antd';

const { Paragraph } = Typography;

//  ant-design Drawer
// 

const ComplainDrawer = () => {
  const [open, setOpen] = useState(false);
  const [size, setSize] = useState();
  const showDrawer = () => {
    setSize('large');
    setOpen(true);
  };
  const onClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Button type="primary" 
              ghost
              defaultGhostColor = '#ffadd2'
              size='middle'
              color='#f0f5ff'
              onClick={showDrawer}
      >
        
      </Button>
      <Drawer 
      size={size}

      title="" placement="right" onClose={onClose} open={open}>
        <div>
          <h2></h2>
          <ul>
            <li> markdown </li>
            <li></li>
            <li> HTML  html </li>
          </ul>
          <Divider/>
          <h2></h2>
          <ul>
            <li></li>
            <li><b></b></li>
            <li><i></i></li>
            <li><b><i></i></b></li>
            <li></li>
          </ul>
          <h3></h3>
          <ul>
            <li><code>``,</code></li>
            <li> []() </li>
            <li></li>
          </ul>

        </div>
        <Divider/>
        <div>
          <h2></h2>
          <p>
            <br></br>
            
          </p>
        </div>
        <Paragraph 
        copyable={{ text: 'https://github.com/Fraserrr/23Autum-round2', 
                    tooltips: ['', '']}}>
             github </Paragraph>
      </Drawer>
    </>
  );
};
export default ComplainDrawer;